<template
	><div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/youtube')">유튜브</li>
			<li @click="goto('/admin/column')" class="current">칼럼</li>
			<li @click="goto('/admin/writer')">칼럼 작성자</li>
		</ul>

		<div class="contents-container">
			<div class="tab-contents">
				<ul class="table board">
					<li class="row props">
						<div class="prop check">
							<input
								type="checkbox"
								class="checkbox"
								name="member"
								id="select-all"
								v-model="selectAll"
							/>
							<label for="select-all"></label>
						</div>
						<div class="prop">대표 이미지</div>
						<div class="prop large">제목</div>
						<div class="prop">작성자</div>
						<div class="prop">작성일자</div>
						<div class="prop">수정</div>
					</li>
					<slot v-if="!columnList">
						<li class="row no-data body3" style="display: block;">
							등록된 글이 없습니다
						</li>
					</slot>
					<slot v-else>
						<li v-for="item in columnList" :key="item.id" class="row">
							<div class="prop check">
								<input
									type="checkbox"
									class="checkbox"
									name="member"
									:id="item.id"
									:value="item.id"
									v-model="selected"
								/>
								<label :for="item.id"></label>
							</div>
							<div class="prop image">
								<img
									src="/resources/upload/column/${column.img_name}"
									width="100"
									height="70"
								/>
							</div>
							<div class="prop large txt-left">
								{{ item.title }}
							</div>
							<div class="prop ">
								{{ item.createId }}
							</div>
							<div class="prop">
								{{ $moment(item.updateDate).format('YYYY.MM.DD') }}
							</div>
							<div class="prop">
								<router-link
									:to="{ name: 'EditColumn', params: { id: item.id } }"
								>
									<Edit />
								</router-link>
							</div>
						</li>
					</slot>
				</ul>
				<div class="bottom-container">
					<Pagination
						v-if="totalPage !== -1"
						:listRowCt="15"
						:pageLinkCt="5"
						:totalCount="totalPage"
						:url="`/admin/column`"
					/>
					<div class="btn-container">
						<div class="checked-info body3 marR-30">
							<span class="number">{{ selectedCnt }}</span
							><span>개 선택됨</span>
						</div>

						<button
							class="solid-btn red"
							@click="isDeleteModalViewed = true"
							:disabled="selected.length < 1"
						>
							선택 글 삭제
						</button>
						<router-link :to="{ name: 'AddColumn' }">
							<button class="solid-btn green">
								새 글 등록
							</button>
						</router-link>
					</div>
					<LoadingSpinner :loading="isLoading" />
				</div>
				<ModalView
					v-if="isDeleteModalViewed"
					@close-modal="isDeleteModalViewed = false"
				>
					<DeleteItem
						@close-modal="isDeleteModalViewed = false"
						@confirm="deleteColumn"
						:count="selected.length"
					></DeleteItem>
				</ModalView>
			</div>
		</div>
	</div>
</template>
<script>
import { fetchColumn, deleteColumn, fetchColumnTotalCount } from '@/api/column';
import Edit from '@/assets/icon/Edit';
import Pagination from '@/components/common/Pagination';
import DeleteItem from '@/views/admin/notice-news/components/DeleteItem';
import ModalView from '@/components/common/ModalView';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: { LoadingSpinner, DeleteItem, Edit, ModalView, Pagination },
	created() {
		this.currentPage = this.$route.params.page;

		this.fetchColumn(this.currentPage);
		this.fetchColumnTotalCount();
	},
	data() {
		return {
			columnList: [],
			selected: [],
			totalPage: -1,
			currentPage: 1,
			isLoading: false,
			isDeleteModalViewed: false,
		};
	},
	computed: {
		selectedCnt() {
			return this.selected.length;
		},
		selectAll: {
			get: function() {
				return this.columnList
					? this.selected.length == this.columnList.length
					: false;
			},
			set: function(value) {
				var selected = [];

				if (value) {
					this.columnList.forEach(function(item) {
						console.log(item.id);
						selected.push(item.id);
					});
				}
				this.selected = selected;
			},
		},
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchColumn(page) {
			this.isLoading = true;
			try {
				const { data } = await fetchColumn(page - 1);
				console.log(page);
				this.columnList = data;
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
		async fetchColumnTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchColumnTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
		async deleteColumn() {
			this.isLoading = true;
			try {
				await deleteColumn(this.selected);
				this.fetchColumn(this.currentPage);
				this.isDeleteModalViewed = false;
				this.selected = [];
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchColumn(this.currentPage);
			}
		},
	},
};
</script>
<style scoped>
.small {
	width: 200px;
}
.middle {
	width: 283px;
}

.large {
	width: 350px;
}
</style>
